import React from 'react';
import PropTypes from 'prop-types';
import Charts from './chart';
import Company from './companys';
import Institutions from './institutions';
import Job from './job';
import Salary from './salary';

/**
 * Render a Funnelback result as a generic card.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function Finder__Results__Generic(props) {
    const company = props.details.metaData.companys ? (
            <Company text={props.details.metaData.companys} />
        ) : null,
        institutions = props.details.metaData.institutions ? (
            <Institutions text={props.details.metaData.institutions} />
        ) : null,
        jobT = props.details.metaData.jobT ? (
            <Job text={props.details.metaData.jobT} />
        ) : null,
        salary = props.details.metaData.salary ? (
            <Salary text={props.details.metaData.salary} />
        ) : null;
    const charts = props.details.metaData.chart.split('|'),
        data = charts.map((d) => JSON.parse(d));

    let count = 0;
    props.details.metaData.companys && count++;
    props.details.metaData.institutions && count++;
    props.details.metaData.jobT && count++;
    props.details.metaData.salary && count++;

    return (
        <div className="destination-leaver">
            <div className="card__details__text">
                <h2 className="card__heading">
                    Showing data for {props.details.title}
                </h2>
                <p className="card__description">
                    {props.details.metaData.c && props.details.metaData.c}
                </p>
            </div>
            <div className="chart-row chart-row--columns">
                {data.map((c, i) => {
                    let k = `${c[0].title}${i}${c[0].id}`;
                    let chartsLength = data.length === 1 ? true : false;
                    return (
                        <Charts
                            key={k}
                            data={c[0].chart}
                            title={c[0].title}
                            id={c[0].id}
                            chartsLength={chartsLength}
                            chartId={parseInt(c[0].id)}
                        />
                    );
                })}
            </div>
            <div className="destination-leaver-content" data-length={count}>
                {salary}
                {company}
                {institutions}
                {jobT}
            </div>
        </div>
    );
}

Finder__Results__Generic.propTypes = {
    details: PropTypes.object,
};

export default Finder__Results__Generic;
