import React from 'react';
import PropTypes from 'prop-types';

/**
 * Render a Funnelback result as a generic card.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function Finder__Chart(props) {
    const data =
        props.data &&
        props.data
            .filter((row) => !!(row[0] && row[1]))
            .map((row) => [row[0], parseFloat(row[1]) || 0]);

    let firstSegmentFull = false,
        secondSegmentFull = false;

    if (data[0][1] === 100) {
        firstSegmentFull = true;
    }
    if (props.chartsLength) {
        if (data[1][1] === 100) {
            secondSegmentFull = true;
        }
    }

    // Total value of data segments (should be 100)
    let sum = data.reduce(function (acc, row) {
        return acc + row[1];
    }, 0);

    let strokeWidth = 32;
    let arcAcc = 0;

    let valueElements = data.map(function (row, i) {
        let value = Math.min(1, row[1] / sum);
        let arc = 2 * Math.PI * value;
        let largeAngle = arc > Math.PI ? 1 : 0;
        let sweep = 1;
        let key = `${props.title}${row[0].replace(/\s+/g, '')}${row[1]}${
            props.chartId
        }`;
        let x0 = 100 * Math.sin(arcAcc);
        let y0 = -100 * Math.cos(arcAcc);
        arcAcc += arc;
        let x1 = 100 * Math.sin(arcAcc);
        let y1 = -100 * Math.cos(arcAcc);
        let d = `M ${x0} ${y0} A 100 100  0 ${largeAngle} ${sweep} ${x1} ${y1}`;

        // If not single segment, build segments proportionately
        if (!(firstSegmentFull || secondSegmentFull) && i < 7) {
            return (
                <path
                    key={key}
                    strokeWidth={strokeWidth}
                    className="chart--piechart__value animate--svg-path"
                    d={d}
                    fill="none"
                />
            );
        } else if (firstSegmentFull) {
            return (
                <React.Fragment>
                    <path
                        key={key}
                        strokeWidth="32"
                        className="chart--piechart__value animate--svg-path animate--svg-path--jsinit animate--svg-path--jsrun"
                        d="M 0 -100 A 100 100  0 1 1 -0.6283143965558805 -99.99802608561372"
                        fill="none"
                    />
                    <path
                        key={key}
                        strokeWidth="32"
                        className="chart--piechart__value animate--svg-path animate--svg-path--jsinit animate--svg-path--jsrun"
                        d="M -0.6283143965558805 -99.99802608561372 A 100 100  0 0 1 -2.4492127076447545e-14 -100"
                        fill="none"
                    />
                </React.Fragment>
            );
        } else if (secondSegmentFull) {
            return (
                <React.Fragment>
                    <path
                        key={key}
                        strokeWidth="32"
                        className="chart--piechart__value animate--svg-path animate--svg-path--jsinit animate--svg-path--jsrun"
                        d="M -0.6283143965558805 -99.99802608561372 A 100 100  0 0 1 -2.4492127076447545e-14 -100"
                        fill="none"
                    ></path>
                    <path
                        key={key}
                        strokeWidth="32"
                        className="chart--piechart__value animate--svg-path animate--svg-path--jsinit animate--svg-path--jsrun"
                        d="M 0 -100 A 100 100  0 1 1 -0.6283143965558805 -99.99802608561372"
                        fill="none"
                    ></path>
                </React.Fragment>
            );
        } else {
            return (
                <path
                    key={key}
                    strokeWidth={strokeWidth}
                    className="chart--piechart__value animate--svg-path"
                    d={d}
                    fill="none"
                    stroke="url(#striped)"
                />
            );
        }
    });

    //let scale = 100 / (100 + strokeWidth / 2);

    let legend = data.map(function (e, i) {
        let val = Math.round((e[1] + Number.EPSILON) * 100) / 100;
        let text = `${e[0]}: ${
            Math.round(((100 * val) / sum + Number.EPSILON) * 100) / 100
        }%`;
        let randomNum = `legend-${props.title}${e[0].replace(/\s+/g, '')}${
            e[1]
        }${props.chartId}`;

        if (i === 7) {
            return (
                <div key={randomNum} className="chart__legend__item">
                    <span className="chart__legend__item__icon">
                        <svg height="23" width="23">
                            <circle
                                cx="12"
                                cy="10"
                                r="9"
                                stroke="black"
                                strokeWidth="1"
                                fill="url(#striped)"
                            />
                        </svg>
                    </span>
                    <span className="chart__legend__item__label">{text}</span>
                </div>
            );
        } else {
            return (
                <div key={randomNum} className="chart__legend__item">
                    <span
                        className="fa fa-circle chart__legend__item__icon"
                        aria-hidden="true"
                    ></span>
                    <span className="chart__legend__item__label">{text}</span>
                </div>
            );
        }
    });

    return (
        <div className="chart">
            <h3 className="chart__title">{props.title}</h3>
            <div className="chart__columns">
                <div className="chart--piechart animate animate-svg">
                    <div className="chart--pie-chart__svg-wrapper">
                        <svg
                            preserveAspectRatio="xMidYMid meet"
                            viewBox="0 0 200 200"
                        >
                            <g transform="translate(100,100) scale(0.8620689655172413)">
                                {valueElements}
                            </g>
                        </svg>
                    </div>
                </div>
                <div className="chart__legend">{legend}</div>
            </div>
        </div>
    );
}

Finder__Chart.propTypes = {
    data: PropTypes.array,
    title: PropTypes.string,
    chartsLength: PropTypes.bool,
    chartId: PropTypes.number,
};

export default Finder__Chart;
