import React from 'react';
import { render } from 'react-dom';
import DestinationHE from './patterns/destination-leavers-higher-ed/destinationHE';
import { finderConfig } from './patterns/finder/funnelback';

document.addEventListener('DOMContentLoaded', () => {
    if (/(Trident|MSIE)/.test(navigator.userAgent)) {
        return;
    } else {
        const destinations = document.querySelectorAll(
            '.wrapper--destination-leavers-HE'
        );
        destinations &&
            Array.from(destinations).forEach((destination) => {
                finderConfig(destination.dataset.config).then((config) => {
                    render(
                        <DestinationHE config={config} element={destination} />,
                        destination
                    );
                });
            });
    }
});
